import { initGame } from "./pages/game";
import { initResults } from "./pages/results";
import { initRules } from "./pages/rules";
import { initWelcome } from "./pages/welcome";

const BASE_PATH = "/Mod5-Desafio-Rock-Papper-Scissors";

function isGithubPages() {
  return location.host.includes("fypoo1day.github.io");
}

const routes = [
  {
    path: /\/welcome/,
    component: initWelcome,
  },
  {
    path: /\/rules/,
    component: initRules,
  },
  {
    path: /\/game/,
    component: initGame,
  },
  {
    path: /\/results/,
    component: initResults,
  },
];

export function initRouter(container: Element) {

  function goTo(path) {
    console.log(isGithubPages());

    const completePath = isGithubPages() ? BASE_PATH + path : path;
    // console.log(completePath);

    history.pushState({}, "", completePath);
    handleRoute(completePath);
  }

  function handleRoute(route) {
    // console.log("el handleRoute route recibió una nueva ruta ", route);

    const newRoute = isGithubPages() ? route.replace(BASE_PATH, "") : route;
    console.log(isGithubPages())
    console.log("Route: " + route)
    console.log("Route1: " +route.replace(BASE_PATH, ""));

    for (const r of routes) {
      console.log("Regex: " + r.path)
      console.log("encontro: " + r.path.test(newRoute))
      if (r.path.test(newRoute)) {
        console.log("pepitos")
        const el = r.component({ goTo: goTo });

        if (container.firstChild) {
          container.firstChild.remove();
        }
        container.appendChild(el);
      }
    }
  }

  // if (location.pathname == "/") {
  //   goTo("/welcome");
  // } else {
  //   console.log("pase")
  //   console.log("Loca: " + location.pathname)
  //   handleRoute(location.pathname);
  // }

  // window.onpopstate = function () {
  //   handleRoute(location.pathname);
  // };

  if (location.pathname == "/Mod5-Desafio-Rock-Papper-Scissors") {
    console.log("entre")
    goTo("/welcome");
  } else {
    console.log("no entre")
    handleRoute(location.pathname);
  }

  window.onpopstate = function () {
    handleRoute(location.pathname);
  };
}
